.header {
  position: fixed;
  width: 100%;
  z-index: 10;
  transition: 0.6s;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0 1.25rem;

  &-wrapper {
    padding: 2.31rem 1.25rem 2.9375rem;
    opacity: 90%;
    width: 100%;
    transition: 0.6s;
    background-image: url(../images/header-1/webP/abstract-background-with-red-lines320.webp);
    background-repeat: no-repeat;
    background-size: 100% 100%;

    @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
      background-image: url(../images/header-1/webP/abstract-background-with-red-lines320@2x.webp);
    }
    @media (min-device-pixel-ratio: 3), (min-resolution: 288dpi), (min-resolution: 3dppx) {
      background-image: url(../images/header-1/webP/abstract-background-with-red-lines320@3x.webp);
    }

    @media screen and (min-width: 768px) {
      padding: 2.5rem 2.1rem 2.81rem;

      background-image: url(../images/header-1/webP/abstract-background-with-red-lines768.webp);

      @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
        background-image: url(../images/header-1/webP/abstract-background-with-red-lines768@2x.webp);
      }
      @media (min-device-pixel-ratio: 3), (min-resolution: 288dpi), (min-resolution: 3dppx) {
        background-image: url(../images/header-1/webP/abstract-background-with-red-lines768@3x.webp);
      }
    }

    @media screen and (min-width: 1280px) {
      max-width: 1280px;
      background-image: url(../images/header-1/tekstura-2-1280px.png);

      @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
        background-image: url(../images/header-1/tekstura-2-1280@2x.png);
      }
      @media (min-device-pixel-ratio: 3), (min-resolution: 288dpi), (min-resolution: 3dppx) {
        background-image: url(../images/header-1/tekstura-2-1280@3x.png);
      }
    }
    &__library {
      opacity: 90%;
      background-image: url(../images/header-2/webP/Rectangle320.webp);
      background-repeat: no-repeat;
      background-size: 100% 100%;
      padding: 2.31rem 1.25rem 2.9375rem;

      @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
        background-image: url(../images/header-2/webP/Rectangle320@2x.webp);
      }
      @media (min-device-pixel-ratio: 3), (min-resolution: 288dpi), (min-resolution: 3dppx) {
        background-image: url(../images/header-2/webP/Rectangle320@3x.webp);
      }

      @media screen and (min-width: 768px) {
        padding: 2.5rem 2.1rem 2.81rem;

        background-image: url(../images/header-2/webP/Rectangle768.webp);

        @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
          background-image: url(../images/header-2/webP/Rectangle768@2x.webp);
        }
        @media (min-device-pixel-ratio: 3), (min-resolution: 288dpi), (min-resolution: 3dppx) {
          background-image: url(../images/header-2/webP/Rectangle768@3x.webp);
        }
      }

      @media screen and (min-width: 1280px) {
        background-image: url(../images/header-2/Rectangle-1280px.png);

        @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
          background-image: url(../images/header-2/Rectangle-1280px@2x.png);
        }
        @media (min-device-pixel-ratio: 3), (min-resolution: 288dpi), (min-resolution: 3dppx) {
          background-image: url(../images/header-2/Rectangle-1280px@3x.png);
        }
      }
    }

    &__fixed {
      display: flex;
      padding: 0.5rem 1.25rem;
      border-radius: 0 0 10px 10px;
    }
  }
  &__container {
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    min-width: 100%;
    height: 100%;
    transition: 0.6s;
    padding-bottom: 54px;
  }
  &__box {
    display: flex;
    justify-content: center;
    align-items: center;
    &-link {
      display: none;
      @media screen and (min-width: 768px) {
        text-decoration: none;
        font-size: 1.875rem;
        line-height: 1.16;
        display: flex;
        align-items: center;
        text-align: center;
        padding-left: 10px;
        color: $background-color;
      }
    }
  }
  &__library {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 0 1.25rem;
  }
  &__fixed {
    position: fixed;
    flex-direction: row;
    opacity: 1;
    &-container {
      min-width: auto;
      padding: 0px;
      transform: 0.6s;
    }
  }
}

